html {
  height: 100%;
}
body {
  /* transition: background-color 1s; */
  background: url(./img/scene.png) no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;  
}
@media (max-width: 400px) {
  body {
    background: url(./img/scene_400.png) no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; 
  }
}
@media (max-width: 800px) {
  body {
    background: url(./img/scene_700.png) no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; 
  }
}
@media (min-width: 1440px) {
  body {
    background: url(./img/scene_1440.png) no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; 
  }
}

/* Main grid rules */
.mainGrid {
  min-height: 100%;
  display: grid;
  grid-template-rows: 1fr auto;  
}
.footer {
  grid-row-start: 2;
  grid-row-end: 3;
  justify-self: center;
}
/* / Main grid rules */

/* Sub grid rules */
.container {
  min-height: 100%;
  display: grid;
  grid-template-rows: auto 2fr;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-areas:
    "header header header" 
    ". main . ";
}
.header {
  grid-column-start: 1;
  grid-column-end: 4;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-areas:
    "logo nav nav"; 
}
.logo {
  grid-area: logo;
  grid-column-start: 1;
  grid-column-end: 2;
  padding-left: 1.5em;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}
.nav {
  grid-area: nav;
  grid-column-start: 2;
  grid-column-end: 4;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}
.main {
  grid-area: main;
  grid-column-start: 2;
  grid-column-end: 3;
}
@media (max-width: 700px) {
  .container {
    min-height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto 1fr;  
    grid-template-areas: 
      "header"
      "main";
  }
  .header {
    grid-column-start: 1;
    grid-column-end: 4;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas:
      "logo"
      "nav"; 
  }
  .logo {
    grid-area: logo;
    grid-row-start: 1;
    grid-row-end: 2; 
    justify-self: center;
    padding-left: 0;
    text-align: center;
    /* margin-top: 0.75em; */
    margin-bottom: 0em;
  }
  .nav {
    grid-area: nav;
    grid-row-start: 2;
    grid-row-end: 3; 
    justify-self: center;
    margin-top: 0em;
    margin-bottom: 0em;
  }
  .main {
    grid-area: main;
    grid-row-start: 2;
    grid-row-end: 3;
    margin-bottom: 1.5em;
  }
}
/* / Sub grid rules */

