.fadeIn {
  /* margin-top: 3.85em; */
  text-align: center;
  opacity: 0;
  display: block;
  animation: slidein 1s ease-out forwards;
}
@keyframes slidein {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
@media (max-width: 700px) {
  /* .fadeIn {
    margin-top: 0.5em;
  } */
  .fadeIn > p {
    margin-left: 1em;
    margin-right: 1em;
  }
  @keyframes slidein {
    0% {
      opacity: 0;
      transform: translateY(-35px);
    }
    100% {
      opacity: 1;
      transform: translateY(0px);
    }
  }
